'use client'
import { useState } from 'react'
import { genAIResponse } from './utils/ai'

function App() {
  const [messages, setMessages] = useState([])
  const [userInput, setUserInput] = useState('')
  const handleInputChange = (e) => setUserInput(e.target.value)

  const handleSubmit = async (e) => {
    e.preventDefault()

    const newMessages = [...messages, { role: 'user', content: userInput }]
    setMessages(newMessages)
    setUserInput('')

    try {
      console.log('==>')
      const aiResponse = await genAIResponse({ data: { messages: newMessages } })
      console.log('<==')
      console.log('aiResponse', aiResponse)
      setMessages((prevMessages) => [...prevMessages, { role: 'assistant', content: aiResponse }])
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <div className="App">
      <div className="assistant-window">
        <div className="header">
          <h3>AI Assistant</h3>
        </div>
        <div className="messages-container">
          <div className="messages-list">
            {messages.map((msg, index) => (
              <div key={index} className={`message ${msg.role === 'assistant' ? 'assistant-msg' : 'user-msg'}`}>
                <div className="message-role">{msg.role === 'assistant' ? 'AI' : 'You'}</div>
                <div className="message-content">{msg.content}</div>
              </div>
            ))}
          </div>
        </div>
        <form onSubmit={handleSubmit} className="input-area">
          <textarea value={userInput} onChange={handleInputChange} placeholder="Type your message..." rows="2" />
          <button type="submit" disabled={!userInput.trim()} className="send-btn">
            Send
          </button>
        </form>
      </div>
    </div>
  )
}

export default App
